.experience-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: wrap;
  border: solid;
  flex-direction: column;
  margin: 5%;
  background-color: rgb(255,255,255, 0.8);
  box-shadow: 10px 10px 0px rgb(0 0 0 / 40%), -10px -10px 0px rgb(255 114 114 / 80%);
}